import { Component, OnInit } from '@angular/core';
import {ScriptService} from '../../core/service/script.service';
import {IScript} from '../../core/model/IScript';
import {Script} from '../../core/model/Script';
import {MatDialog, MatDialogRef} from '@angular/material';
import {DataScriptService} from '../../core/service/data-script.service';

@Component({
  selector: 'app-add-edit-script',
  templateUrl: './add-edit-script.component.html',
  styleUrls: ['./add-edit-script.component.css']
})
export class AddEditScriptComponent implements OnInit {

  pageTitle: string = 'Ajouter un script vocal';
  script: IScript ={
        vscId: '',
        vscDialPlanNumber: 0,
        vscName: '',
        vscType: 0,
        vscScript: ''
      };
  scriptKey: Script;
  scriptsTypes = [{name: 'DTMF', value: 0}, {name: 'Enregistrement', value: 1}];
  dtmfKeys = [ {value: '#'},
                { value: '*'},
                { value: '0'},
                { value: '1'},
                { value: '2'},
                { value: '3'},
                { value: '4'},
                { value: '5'},
                { value: '6'},
                { value: '7'},
                { value: '8'},
                { value: '9'}
                ];
  selectedFiles: FileList;
  progress: { percentage: number } = { percentage: 0 };

  constructor(public scriptService: ScriptService,
              public dialogRef: MatDialogRef<AddEditScriptComponent>,
              private dialog: MatDialog) { }

  ngOnInit() {
  }

  onSubmit() {
      //console.log(this.scriptService.scriptForm.get('dtmfCodes').value[0]);
     // console.log(this.selectedFiles.item(0).name.split("."[0]));
      let fileCurrent = null;
      if(this.selectedFiles) fileCurrent =this.selectedFiles.item(0);
    this.initializeScript();
    this.scriptService.addScript(this.script,fileCurrent).subscribe(()=>{
      console.log("OK");
    });




  }

  initializeScript() : void {
    this.scriptKey = new Script();
    if(1 == this.scriptService.scriptForm.get('vscType').value){
      let record = this.scriptService.scriptForm.get('records').value[0];
      this.scriptKey.replay_key="";
      this.scriptKey.message_error="";
      this.scriptKey.max_error="";
      this.scriptKey.signal_before_recording=record.signal_before_recording;
      this.scriptKey.silence_record_timeout= record.silence_record_timeout;
    } else {
      let dtmfCodes = this.scriptService.scriptForm.get('dtmfCodes').value[0];
      this.scriptKey.replay_key=dtmfCodes.vocalScriptKey;
      this.scriptKey.message_error="";
      this.scriptKey.max_error=dtmfCodes.maxError;
      this.scriptKey.signal_before_recording="";
      this.scriptKey.silence_record_timeout= "";
    }

    this.script.vscDialPlanNumber = this.scriptService.scriptForm.get('vscDialPlanNumber').value;
    this.script.vscName = this.scriptService.scriptForm.get('vscName').value;
    this.script.vscType = this.scriptService.scriptForm.get('vscType').value;
    this.script.vscScript = JSON.stringify(this.scriptKey);

  }

  selectFile(event) {
    this.selectedFiles = event.target.files;
  }

  onClose() {
    this.scriptService.scriptForm.reset();
    this.dialogRef.close();
  }


}
