import {Component, OnInit} from '@angular/core';
import {AbstractControl, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {debounceTime} from 'rxjs/operators';
import {IUser} from '../../core/model/User';
import {UserService} from '../../core/service/user.service';
import {AlertService} from 'src/app/core/service/alert.service';


function passwordMatcher(c: AbstractControl): { [key: string]: boolean } | null {
  const emailControl = c.get('usr_password');
  const confirmControl = c.get('confirmPassword');
  if (emailControl.pristine || confirmControl.pristine) {
    return null;
  }
  if (emailControl.value === confirmControl.value) {
    return null;
  }
  return {'match': true};
}

@Component({
  selector: 'app-user-management',
  templateUrl: './user-management.component.html',
  styleUrls: ['./user-management.component.css']
})
export class UserManagementComponent implements OnInit {

  langues = [{name: 'Français', value: 0}, {name: 'Anglais', value: 1}];
  userForm: FormGroup;
  user: IUser;
  passwordMessage: string;
  emailMessage: string;


  private passwordValidationMessages = {
    required: 'Le mot de passe est obligatoire.',
    pattern: 'Le mot de passe doit être de 8 caractères minimum et contenir au moins 1 chiffre,' +
      'combinant des lettres majuscules et minuscules et au moins un chiffre et 1 caractère spécial.'
  };

  private emailValidationMessages = {
    required: 'L\'adrese email est obligatoire.',
    pattern: 'l\'email n\'est pas valide.'
  };

  constructor(private userservice: UserService,
              private fb: FormBuilder,
              private alertService: AlertService) {
  }

  ngOnInit() {
    this.userForm = this.fb.group({
      passwordGroup: this.fb.group({
        usr_password: ['', [Validators.required, Validators.pattern('(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[$@$!%*?&])[A-Za-z\\d$@$!%*?&].{8,}')]],
        confirmPassword: ['', Validators.required],
      }, {validator: passwordMatcher}),
      usr_email: ['', [Validators.required, Validators.pattern('[a-z0-9._%+-]+@[a-z0-9.-]+')]],
      usr_default_language: [this.langues[0].value, [Validators.required]]
    });

    this.user = JSON.parse(localStorage.getItem('User'));
    // Update the data on the form
    this.setCurrentUser();
    // password validator
    const passwordControl = this.userForm.get('passwordGroup.usr_password');
    passwordControl.valueChanges.pipe(debounceTime(1000)).subscribe(value =>
      this.setMessage(passwordControl, 0));

    const emailControl = this.userForm.get('usr_email');
    emailControl.valueChanges.pipe(debounceTime(1000)).subscribe(value =>
      this.setMessage(emailControl, 1));
  }

  save():void {
    this.user.usr_login = 'admin';
    this.user.usr_email = this.userForm.get('usr_email').value;
    this.user.usr_password = this.userForm.get('passwordGroup.usr_password').value;
    this.user.usr_default_language = this.userForm.get('usr_default_language').value;
    console.log(JSON.stringify(this.user));
    this.userservice.updateUser(this.user).subscribe(user => {
        this.alertService.success('La modification du profil utilisateur a été effective', true);
      },
      error => {
        this.alertService.error("Erreur Système! Merci de contacter l'administrateur");
      });

  }


  setMessage(c: AbstractControl, id: number): void {
    this.passwordMessage = '';
    this.emailMessage = '';
    if ((c.touched || c.dirty) && c.errors) {
      if (0 === id) {
        this.passwordMessage = Object.keys(c.errors).map(key =>
          this.passwordValidationMessages[key]).join(' ');
      } else {
        this.emailMessage = Object.keys(c.errors).map(key =>
          this.emailValidationMessages[key]).join(' ');
      }
    }
  }

  setCurrentUser(): void{
    this.userForm.patchValue({
      passwordGroup: ({
        usr_password: this.user.usr_password,
        confirmPassword: this.user.usr_password,
      }),
      usr_email: this.user.usr_email,
      usr_default_language: this.user.usr_default_language
    }); 
  }

  onCancel():void {
    this.setCurrentUser();
  }

}
