import { Component, OnInit } from '@angular/core';
import {DialerService} from '../../core/service/dialer.service';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {AlertService} from '../../core/service/alert.service';
import {IDialerSetting} from '../../core/model/IDialerSetting';

@Component({
  selector: 'app-dialer-setting',
  templateUrl: './dialer-setting.component.html',
  styleUrls: ['./dialer-setting.component.css']
})
export class DialerSettingComponent implements OnInit {

  dialerForm: FormGroup;
  dialer: IDialerSetting;
  constructor(private dialerService: DialerService,
              private fb: FormBuilder,
              private alertService: AlertService) { }

  ngOnInit() {
    this.dialerForm =  this.fb.group({
      maxCall:  ['', [Validators.required]],
      maxNumber: ['', [Validators.required]],
      waitDuration: ['', [Validators.required]],
      amdDetection: [true]
    });

    this.dialerService.getDialer().subscribe(dialer =>{
      this.dialer = dialer;
      this.setCurrentDialer();
    })


  }

  update() {
    if (this.dialerForm.valid) {
      // Copy the form values over the product object values
      Object.assign(this.dialer, this.dialerForm.value);

        this.dialerService.updateDialer(this.dialer).subscribe((dialer) => {
          this.alertService.success('Le dialer a été modifier avec succès.'
            , true);
        },error => {
          this.alertService.error('erreur système merci de réesayer plus tard.'
          , true);
        });
      }

  }

  setCurrentDialer(): void {
    this.dialerForm.patchValue({
      maxCall:  this.dialer.maxCall,
      maxNumber: this.dialer.maxNumber,
      waitDuration: this.dialer.waitDuration,
      amdDetection: this.dialer.amdDetection
    });
  }
  
  onCancel():void {
    this.setCurrentDialer();
  }

}
