import {Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {MatDialog, MatDialogConfig, MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import {AlertService} from 'src/app/core/service/alert.service';
import {Router} from '@angular/router';
import {SipProvidersService} from 'src/app/core/service/sip-providers.service';
import {ISipProviders} from 'src/app/core/model/ISipProviders';
import {AddEditSipComponent} from './add-edit-sip.component';
import {DialogService} from 'src/app/core/service/dialog.service';
import {DataSipService} from '../../core/service/data-sip.service';


@Component({
  selector: 'app-list-sip',
  templateUrl: './list-sip.component.html',
  styleUrls: ['./list-sip.component.css']
})
export class ListSipComponent implements OnInit,OnDestroy {
  dialogConfig : MatDialogConfig;
  navigationSubscription;
  sipProvider: ISipProviders;
  sipProviders: ISipProviders[];
  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['pvd_NAME',
    'pvd_LOGIN',
    'pvd_AUTHORIZATIONUSERNAME',
    'pvd_SIPSERVERADDRESS',
    'pvd_SIPSERVERPORT',
    'pvd_CONCURRENTCALLS',
    'pvd_CALLERID',
    'actions'];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  searchKey: string;

  constructor(private alertService: AlertService,
              private router: Router,
              private sipService: SipProvidersService,
              private dataSipService: DataSipService,
              private dialog: MatDialog,
              private dialogService: DialogService
  ) {
  }


  ngOnInit() {
    this.dataSipService.getListISipProviders().subscribe(sips => {
       this.sipProviders = (sips) ? sips : this.sipProviders = [];
        this.listData = new MatTableDataSource(this.sipProviders);
        this.listData.sort = this.sort;
        this.listData.paginator = this.paginator;
        this.listData.filterPredicate = (data, filter) => {
          return this.displayedColumns.some(ele => {
            return ele != 'actions' && data[ele].toLowerCase().indexOf(filter) != -1;
          });
        };
      },
      error => {
        this.alertService.error(error);
      });
  }


  applyFilter() {
    this.listData.filter = this.searchKey.trim().toLowerCase();
  }


  onCreate() {
    this.sipService.initializeFormGroup();
    this.configDialog();
    this.dialog.open(AddEditSipComponent, this.dialogConfig);
  }

  onEdit(row) {
    this.sipService.populateForm(row);
    this.configDialog();
    this.dialog.open(AddEditSipComponent, this.dialogConfig);
  }

  onDelete(sipId) {
    console.log("ID"+ sipId);
    this.dialogService.openConfirmDialog('Êtes vous sûr de vouloir supprimer ?')
      .afterClosed().subscribe(res => {
      if (res) {
        this.sipService.deleteSipProviders(sipId).subscribe(sip => {
            this.alertService.success('le fournisseur a été supprimer avec succès.'
              , true);
            this.dataSipService.notifySubscribers();
              },
          error => {
            this.alertService.error(error);
          });
      }
    });
  }

  configDialog(){
    this.dialogConfig = new MatDialogConfig();
    //dialogConfig.disableClose = true;
    this.dialogConfig.autoFocus = true;
    this.dialogConfig.width = '40%';
  }

  ngOnDestroy(): void {
    this.sipProviders=[];
  }
}

