import {Injectable, OnInit} from '@angular/core';
import {Observable, Subject} from 'rxjs';
import {ISipProviders} from '../model/ISipProviders';
import {SipProvidersService} from './sip-providers.service';

@Injectable({
  providedIn : 'root'
})
export class DataSipService implements OnInit {

  private subjectListSip: Subject<ISipProviders[]> = new Subject<ISipProviders[]>();

  constructor(private sipService: SipProvidersService) {
    this.initializeList();
  }

  ngOnInit(): void {
    this.initializeList();
  }

  getListISipProviders(): Observable<ISipProviders[]> {
    return this.subjectListSip.asObservable();
  }

  public notifySubscribers() {
    this.initializeList();
  }

  private initializeList() {
    this.sipService.getAllSipProviders().subscribe(sips => {
      this.subjectListSip.next(sips);
      console.log(JSON.stringify(sips));
    });
  }

}
