import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {IUser} from '../core/model/User';
import {Router} from '@angular/router';
import {AuthService} from '../auth/auth.service';
import {AlertService} from '../core/service/alert.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginFormTitle = 'Pondeuse d\'appel';
  loginForm: FormGroup;
  user: IUser;

  constructor(private fb: FormBuilder,
              private authservice: AuthService,
              private route: Router,
              private alertService: AlertService) {
    this.loginForm = this.fb.group({
      usr_login: ['', [Validators.required]],
      usr_password: ['', [Validators.required]],
    });
  }

  ngOnInit() {
    if (this.authservice.isLoggednIn()) {
      this.route.navigate(['dashboard']);
    }
  }

  login() {
    this.authservice.login(this.loginForm.get('usr_login').value, this.loginForm.get('usr_password').value)
      .subscribe(user => {
          this.user = user;
          /*console.log(this.loginForm.get('usr_login').value + 'Saved: ' + this.loginForm.get('usr_password').value);
          console.log(this.user);*/
          this.authservice.sendToken('TOKEN');
          localStorage.setItem('User', JSON.stringify(this.user));
          this.alertService.success('Bienvenue dans l\'application de pondeuse d\'appel', true);
          this.route.navigate(['dashboard']);
          this.authservice.loggedIn.next(true);
        },
        error => {
          this.alertService.error('votre identifiant ou votre mot de passe est incorrect');
        });

    /*this.console.log(this.user);
    console.log('Saved: ' + JSON.stringify());*/
  }

}
