import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {IScript} from '../model/IScript';
import {environment} from '../../../environments/environment';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';


const API_URL = environment.apiUrl;

@Injectable({
  providedIn: 'root'
})
export class ScriptService {

  scriptForm: FormGroup = this.fb.group({
    vscId: '',
    vscDialPlanNumber: ['', Validators.required],
    vscName: ['', Validators.required],
    vscType: [0, Validators.required],
    vscScript: ['', Validators.required],
    records: this.fb.array([this.buildRecord()]),
    dtmfCodes: this.fb.array([this.buidDtmf()]),
    messageAudio: ['', Validators.required]
  });

  constructor(private http: HttpClient, private fb: FormBuilder) {
  }

  get records(): FormArray {
    return <FormArray>this.scriptForm.get('records');
  }

  get dtmfCodes(): FormArray {
    return <FormArray>this.scriptForm.get('dtmfCodes');
  }

  getAllScripts(): Observable<IScript[]> {
    return this.http.get<IScript[]>(`${API_URL}/vocalscript/getall`);
  }

  addScript(newScript: IScript, file: File): Observable<IScript> {
    let data = {
      'vscDialPlanNumber': newScript.vscDialPlanNumber.toString(),
      'vscName': newScript.vscName,
      'vscType': newScript.vscType.toString(),
      'vscScript': newScript.vscScript
    };
    let formData = new FormData();
    if (file) formData.append('file', file);

    return this.http.post<IScript>(`${API_URL}/vocalscript/add`,
      formData, {
        params: data
      });

  }

  updateScript(updateScript: IScript): Observable<IScript> {
    return this.http.put<IScript>(`${API_URL}/vocalscript/${updateScript.vscId}/update`, updateScript);
  }

  deleteScript(scriptId: string): Observable<IScript> {
    return this.http.delete<IScript>(`${API_URL}/vocalscript/${scriptId}/delete`);
  }

  buildRecord(): FormGroup {
    return this.fb.group({
      signal_before_recording: true,
      silence_record_timeout: ['', Validators.required]
    });
  }

  buidDtmf(): FormGroup {
    return this.fb.group({
      vocalScriptKey: ['', Validators.required],
      file: [null, Validators.required],
      maxError: ['', Validators.required]
    });
  }
}
