import {Injectable} from '@angular/core';
import {environment} from '../../../environments/environment';
import {ISipProviders} from '../model/ISipProviders';
import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import * as _ from 'lodash';

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: 'root'
})
export class SipProvidersService {
  

  constructor(private http: HttpClient,private fb: FormBuilder) {
  }

  addSipProviders(newSip: ISipProviders): Observable<ISipProviders> {
    return this.http.post<ISipProviders>(`${API_URL}/sipprovider/add`, newSip);
  }

  updateSipProviders(updatedSip: ISipProviders): Observable<void> {
    return this.http.put<void>(`${API_URL}/sipprovider/${updatedSip.pvd_ID}/update`, updatedSip);
  }

  deleteSipProviders(sipId: string): Observable<ISipProviders> {
    return this.http.delete<ISipProviders>(`${API_URL}/sipprovider/${sipId}/delete`);
  }

  getAllSipProviders(): Observable<ISipProviders[]> {
    console.log('Getting all from the server.');
    return this.http.get<ISipProviders[]>(`${API_URL}/sipprovider/getall`);
  }

  //because I initialize and populate in ListSipComponent and AddEditComponent
  sipForm : FormGroup = this.fb.group({
    pvd_ID: '',
    pvd_NAME: ['', Validators.required],
    pvd_AUTHORIZATIONUSERNAME: ['', Validators.required],
    pvd_SIPSERVERADDRESS: ['', Validators.required],
    pvd_SIPSERVERPORT: ['', Validators.required],
    pvd_PASSWORD: ['', Validators.required],
    pvd_LOGIN: ['', Validators.required],
    pvd_CONCURRENTCALLS: ['', Validators.required],
    pvd_CALLERID: [''],
    pvd_OUTBOUNDPROXY: [''],
  });

  initializeFormGroup() {
    this.sipForm.setValue({
        pvd_ID: '',
        pvd_NAME: '',
        pvd_AUTHORIZATIONUSERNAME: '',
        pvd_SIPSERVERADDRESS: '',
        pvd_SIPSERVERPORT: '',
        pvd_PASSWORD: '',
        pvd_LOGIN: '',
        pvd_CONCURRENTCALLS: '',
        pvd_CALLERID: '',
        pvd_OUTBOUNDPROXY: ''
      });
  }

  initializeSip(): ISipProviders {
    // Return an initialized object
    return {
      pvd_ID: 0,
      pvd_NAME: null,
      pvd_AUTHORIZATIONUSERNAME: null,
      pvd_SIPSERVERADDRESS: null,
      pvd_SIPSERVERPORT: 0,
      pvd_PASSWORD: null,
      pvd_LOGIN: null,
      pvd_CONCURRENTCALLS: null,
      pvd_CALLERID: null,
      pvd_OUTBOUNDPROXY: null
    };
  }
  populateForm(newSip: ISipProviders) {
    this.sipForm.setValue(newSip);
  }

}
