import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {IDialerSetting} from '../model/IDialerSetting';
import {environment} from '../../../environments/environment';

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: 'root'
})
export class DialerService {

  constructor(private http: HttpClient) { }

  updateDialer(updateDialer:IDialerSetting): Observable<IDialerSetting> {
    return this.http.put<IDialerSetting>(`${API_URL}/dialer/update`, updateDialer);
  }

  getDialer() : Observable<IDialerSetting> {
    return this.http.get<IDialerSetting>(`${API_URL}/dialer/getsettings`);
  }
}
