import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ISipProviders} from '../../core/model/ISipProviders';
import {Router} from '@angular/router';
import {SipProvidersService} from '../../core/service/sip-providers.service';
import {AlertService} from '../../core/service/alert.service';
import { MatDialogRef } from '@angular/material';
import {DataSipService} from '../../core/service/data-sip.service';

@Component({
  selector: 'app-add-edit-sip',
  templateUrl: './add-edit-sip.component.html',
  styleUrls: ['./add-edit-sip.component.css']
})
export class AddEditSipComponent implements OnInit { 
  pageTitle: string = 'Ajouter un fournisseur Sip';
  sipProviders: ISipProviders;

  constructor(private fb: FormBuilder,
              private alertService: AlertService,
              private router: Router,
              public sipService: SipProvidersService,
              private dataSipService: DataSipService,
              public dialogRef: MatDialogRef<AddEditSipComponent>
  ) {
  }

  ngOnInit() {
    if(this.sipService.sipForm.get("pvd_ID").value) 
    this.pageTitle = "Modification du Fournisseur Sip";
    this.sipProviders = this.sipService.initializeSip();
  }

  onSubmit() {
    
    if (this.sipService.sipForm.dirty && this.sipService.sipForm.valid) {
      // Copy the form values over the product object values
      const sip = Object.assign({}, this.sipProviders, this.sipService.sipForm.value);
      if (!sip.pvd_ID) {    
       this.sipService.addSipProviders(sip)
        .subscribe(() => {
          this.alertService.success('le fournisseur ' + sip.pvd_NAME +
            ' a été ajouter avec succès.'
            , true);
          this.onClose();
          //refresh list
          this.dataSipService.notifySubscribers();

        },error => {
          this.alertService.error('erreur système merci de réesayer plus tard.'
            , true);
        });
      } else {
        this.sipService.updateSipProviders(sip).subscribe(() => {
          this.alertService.success('le fournisseur ' + sip.pvd_NAME +
            ' a été modifier avec succès.'
            , true);
          this.onClose();
          //refresh list
          this.dataSipService.notifySubscribers();
        },error => {
          this.alertService.error('erreur système merci de réesayer plus tard.'
            , true);
        });
      }

    } else if (!this.sipService.sipForm.dirty) {
      this.onClose();
    }
  }

  onClose() {
    this.sipService.sipForm.reset();
    this.sipService.initializeFormGroup();
    this.dialogRef.close();
  }

}
