import {Injectable, OnInit} from '@angular/core';
import {ScriptService} from './script.service';
import {Observable, Subject} from 'rxjs';
import {IScript} from '../model/IScript';
import {ISipProviders} from '../model/ISipProviders';

@Injectable({
  providedIn: 'root'
})
export class DataScriptService  {
  private subjectListScript : Subject<IScript[]> = new Subject<IScript[]>();

  constructor(private scriptService: ScriptService) {
    this.initializeList();
  }

  getListScripts() : Observable<IScript[]>  {
    return this.subjectListScript.asObservable();
  }

  private initializeList(){
    this.scriptService.getAllScripts().subscribe(scripts =>{
      this.subjectListScript.next(scripts);
    });
  }

  public notifySubscribers() {
    this.initializeList();
  }
}
