import {Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {ISipProviders} from '../../core/model/ISipProviders';
import {MatDialog, MatDialogConfig, MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import {IScript} from '../../core/model/IScript';
import {DataScriptService} from '../../core/service/data-script.service';
import {AddEditSipComponent} from '../sip-management/add-edit-sip.component';
import {AddEditScriptComponent} from './add-edit-script.component';
import {AlertService} from '../../core/service/alert.service';

@Component({
  selector: 'app-list-script',
  templateUrl: './list-script.component.html',
  styleUrls: ['./list-script.component.css']
})
export class ListScriptComponent implements OnInit,OnDestroy {

  scripts: IScript[];
  listData: MatTableDataSource<any>;
  displayedColumns: string[] = ['vscName',
                                'vscDialPlanNumber',
                                'vscType',
                                'actions'];
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  dialogConfig: MatDialogConfig;
  searchKey: string;

  constructor(private alertService: AlertService,
              private dataScript : DataScriptService,
              private dialog: MatDialog) {
    this.dataScript.getListScripts().subscribe(scripts=>{
      this.scripts = scripts;
      this.listData = new MatTableDataSource(this.scripts);
      this.listData.sort = this.sort;
      this.listData.paginator = this.paginator;
      this.listData.filterPredicate = (data, filter) => {
        return this.displayedColumns.some(ele => {
          return ele != 'actions' && data[ele].toLowerCase().indexOf(filter) != -1;
        });
      };
    },
      error => {
        this.alertService.error('erreur système merci de réesayer plus tard.'
          , true);
      })
  }

  ngOnInit() {
  }

  onCreate() {
    this.configDialog();
    this.dialog.open(AddEditScriptComponent, this.dialogConfig);
  }

  onEdit(row) {
    //this.sipService.populateForm(row);
    this.configDialog();
    this.dialog.open(AddEditScriptComponent, this.dialogConfig);
  }

  ngOnDestroy(): void {
    this.scripts = [];
  }

  configDialog(){
    this.dialogConfig = new MatDialogConfig();
    //dialogConfig.disableClose = true;
    this.dialogConfig.autoFocus = true;
    this.dialogConfig.width = '60%';
  }

}
